"use client";

import { useCallback, useEffect, useMemo, useState } from "react";
import { Filter } from "lucide-react";

import { AdminProtected } from "@/components/admin/AdminProtected";
import { CmsListLayout, type StatusTab } from "@/components/admin/cms/CmsListLayout";
import { CaseStudyTable, type CaseStudyTableItem } from "@/components/admin/cms/CaseStudyTable";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

type CaseStudyListState = {
  loading: boolean;
  error: string | null;
  items: CaseStudyTableItem[];
  tags: string[];
};

const STATUS_TABS: StatusTab[] = [
  { value: "all", label: "すべて" },
  { value: "published", label: "公開" },
  { value: "draft", label: "下書き" },
];

type StatusFilter = (typeof STATUS_TABS)[number]["value"];

function CaseStudyListContent({ supabase }: { supabase: any }) {
  const [state, setState] = useState<CaseStudyListState>({
    loading: true,
    error: null,
    items: [],
    tags: [],
  });
  const [search, setSearch] = useState("");
  const [status, setStatus] = useState<StatusFilter>("all");
  const [tagFilter, setTagFilter] = useState<string>("すべて");

  const loadCaseStudies = useCallback(async () => {
    setState((prev) => ({ ...prev, loading: true, error: null }));

    const { data, error } = await supabase
      .from("cms_entries")
      .select("id, type, slug, title, status, published_at, excerpt, tags, cover_image_url")
      .eq("type", "case_study")
      .order("published_at", { ascending: false, nullsFirst: false });

    console.log("[AdminCaseStudies] list result", { data, error });

    if (error) {
      console.error("[AdminCaseStudies] 導入事例の取得に失敗しました", error);
      setState((prev) => ({ ...prev, loading: false, error: "導入事例の取得に失敗しました" }));
      return;
    }

    const rows = (data ?? []) as any[];
    const items: CaseStudyTableItem[] = rows.map((row) => {
      const tags: string[] = Array.isArray(row.tags)
        ? (row.tags as string[])
        : [];

      const primaryCategory = tags.length > 0 ? tags[0] : null;

      return {
        id: row.id,
        title: row.title,
        slug: row.slug,
        summary: row.excerpt ?? null,
        category: primaryCategory,
        status: row.status ?? "draft",
        published_at: row.published_at ?? null,
        thumbnail_url: row.cover_image_url ?? null,
      };
    });

    const tagSet = new Set<string>();
    items.forEach((item) => {
      if (item.category) {
        tagSet.add(item.category);
      }
    });

    setState({ loading: false, error: null, items, tags: Array.from(tagSet).sort() });
  }, [supabase]);

  useEffect(() => {
    loadCaseStudies();
  }, [loadCaseStudies]);

  const filteredItems = useMemo(() => {
    return state.items.filter((item) => {
      const matchesSearch = search
        ? `${item.title} ${item.slug ?? ""} ${item.summary ?? ""}`
            .toLowerCase()
            .includes(search.toLowerCase())
        : true;
      const matchesStatus =
        status === "all" ? true : status === "published" ? item.status === "published" : item.status !== "published";
      const matchesTag =
        tagFilter === "すべて" ? true : (item.category ?? "").toLowerCase() === tagFilter.toLowerCase();

      return matchesSearch && matchesStatus && matchesTag;
    });
  }, [search, state.items, status, tagFilter]);

  const tagOptions = useMemo(() => ["すべて", ...state.tags], [state.tags]);

  if (state.loading) {
    return (
      <div className="p-8">
        <div className="rounded-2xl border border-slate-200 bg-white p-8 text-center text-slate-600">
          導入事例を読み込んでいます…
        </div>
      </div>
    );
  }

  if (state.error) {
    return (
      <div className="p-8">
        <div className="space-y-4 rounded-2xl border border-red-500/40 bg-red-500/10 p-8 text-center text-red-900">
          <p>{state.error}</p>
          <Button variant="outline" className="border-red-300 text-red-900" onClick={loadCaseStudies}>
            再読み込み
          </Button>
        </div>
      </div>
    );
  }

  const tagFilterControl = (
    <div className="flex flex-wrap items-center gap-3">
      <span className="text-xs uppercase tracking-[0.3em] text-slate-400">タグ</span>
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="outline" className="w-full justify-between border-slate-300 bg-white text-slate-700 md:w-64">
            <span>{tagFilter}</span>
            <Filter className="h-4 w-4" />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="end" className="w-64">
          <DropdownMenuLabel>タグで絞り込み</DropdownMenuLabel>
          <DropdownMenuSeparator />
          {tagOptions.map((option) => (
            <DropdownMenuItem key={option} onSelect={() => setTagFilter(option)}>
              {option}
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  );

  return (
    <div className="space-y-10 p-8">
      <CmsListLayout
        title="導入事例"
        description="導入事例コンテンツの公開・下書きを管理します。"
        searchPlaceholder="タイトルやスラッグで検索"
        searchValue={search}
        onSearchChange={setSearch}
        statusTabs={STATUS_TABS}
        statusValue={status}
        onStatusChange={(value) => setStatus(value as StatusFilter)}
        createHref="/admin/cms/case-studies/new"
        createLabel="新規作成"
        secondaryFilters={tagFilterControl}
      >
        <CaseStudyTable items={filteredItems} />
      </CmsListLayout>
    </div>
  );
}

export default function AdminCaseStudyListPage() {
  return (
    <AdminProtected>
      {({ supabase }) => <CaseStudyListContent supabase={supabase} />}
    </AdminProtected>
  );
}

export const dynamic = 'force-dynamic';
